.search-result {
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -43px;
    max-height: 900px;
    overflow-y: auto;
    &::-webkit-scrollbar-track {
      border-radius: 10px;
      background-color: transparent;
    }

    &::-webkit-scrollbar {
      width: 12px;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: $main-color;
    }
  }
  &__column {
    flex: 0 0 25%;
    padding: 20px 43px;
  }
}

@include media-extra-large {
  .search-result {
    &__row {
      margin: 0 -10px;
    }
    &__column {
      padding: 20px;
    }
  }
}

@include media-large {
  .search-result {
    &__column {
      flex: 0 0 33%;
    }
  }
}

@include media-small {
  .search-result {
    &__column {
      flex: 0 0 50%;
    }
  }
}
