.articles {
  padding: 81px 0 107px;
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    margin-bottom: 80px;
  }
  &__item {
    flex: 0 0 33%;
    padding: 15px;
    min-width: 320px;
  }
  &__footer {
    display: flex;
    justify-content: center;
  }
}

.articles-item {
  position: relative;
  padding: 108px 8px 20px 20px;
  border-radius: 10px;
  overflow: hidden;
  &__bg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &::before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba($color: #000000, $alpha: 0.4);
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__title {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 18px;
    line-height: 28px;
    color: $sub-color;
    text-decoration: none;
  }
  &__text {
    font-size: 16px;
    line-height: 24px;
    color: $sub-color;
    text-decoration: none;
  }
}

@include media-extra-large {
  .articles-item {
    &__title {
      font-size: 16px;
    }
  }
}

@include media-medium {
  .articles {
    padding: 43px 0 41px;

    &__row {
      margin: 0;
      margin-bottom: 24px;
    }
    &__link {
      font-size: 14px;
      padding: 12px 37px;
    }
  }
  .articles__item {
    padding: 7.5px 0;
    flex: 0 0 60%;
    margin: 0 auto;
    min-width: 0;
  }
}

@include media-small {
  .articles__item {
    flex: 0 0 100%;
  }
  .articles-item {
    &__title {
      font-size: 14px;
      line-height: 1.2em;
    }
    &__text {
      font-size: 12px;
      line-height: 14px;
    }
  }
}
