.customers {
  &__header {
    margin-bottom: 94px;
  }
  &__add-customers {
    padding: 20px 40px;
  }
  &__customers {
    margin: 0 -47px;
  }
  &__column {
    flex: 0 0 33%;
    padding: 0 47px;
    margin-bottom: 30px;
  }
}

@include media-large {
  .customers {
    &__customers {
      margin: 0 -20px;
    }
    &__column {
      padding: 20px;
      flex: 0 0 50%;
    }
  }
}

@include media-small {
  .customers {
    &__customers {
      margin: 0 0px;
    }
    &__column {
      padding: 0px;
      flex: 0 0 80%;
      margin: 0 auto;
      margin-bottom: 30px;
    }
  }
}
