.cosplayer-services {
    &__row {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-bottom: -30px;
    }
    &__column {
        flex: 0 1 500px;
        display: flex;
        margin-bottom: 30px;
    }
}

.cosplayer-services-item {
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 0.04em;
    &__icon {
        width: 40px;
        height: 40px;
        margin-right: 24px;
    }
}

@include media-small {
    .cosplayer-services-item {
        font-size: 16px;
    }
}