.footer {
  background: #303030;
  padding: 110px 0 80px;
  &__row {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    margin-right: 95px;
    margin-bottom: 20px;
    &:first-child {
      margin-right: auto;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__logo {
    width: 133px;
    height: 78px;
    margin-bottom: 31px;
  }
  &__copirate {
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.01em;
    color: #808080;
  }
}

.footer-social {
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 31px;
  &__item {
    margin-right: 12px;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4px;
    border-radius: 50%;
    background: #808080;
    img {
      width: 20px;
      height: 20px;
    }
  }
}

.footer-item {
  &__title {
    margin-bottom: 34px;
    font-weight: bold;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.05em;
    color: #cfcfcf;
  }
  &__list {
    padding: 0;
    list-style: none;
  }
  &__item {
    margin-bottom: 14px;
  }
  &__link {
    text-decoration: none;
    font-size: 20px;
    letter-spacing: 0.01em;
    color: #cfcfcf;
    &:hover {
      text-decoration: underline;
    }
  }
}

@include media-medium {
  .footer {
    padding: 44px 0;
    &__logo {
      width: 79px;
      margin: 0;
    }
    &__row {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__copirate {
      font-size: 12px;
      line-height: 10px;
      letter-spacing: 0.01em;
    }
    & &__item {
      margin: 0;
      margin-bottom: 23px;
      &:nth-child(1) {
        order: 4;
      }
      &:nth-child(2) {
        order: 1;
      }
      &:nth-child(3) {
        order: 3;
      }
      &:nth-child(4) {
        order: 2;
      }
    }
  }

  .footer-item {
    &__title {
      margin-bottom: 8px;
      font-weight: bold;
      font-size: 16px;
      line-height: 11px;
      letter-spacing: 0.05em;
    }
    &__item {
      margin-bottom: 4px;
    }
    &__link {
      font-size: 14px;
      line-height: 1em;
      text-align: center;
      letter-spacing: 0.01em;
      color: #cfcfcf;
    }
  }
}
