.news {
  padding: 79px 0 78px;
  &__container {
    max-width: 996px;
    margin: 0 auto;
  }
  &__header {
    position: relative;
    max-width: 981px;
    margin: 72px 0;

    &::before {
      content: '';
      display: block;
      padding-bottom: 56%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.news-title {
  text-align: center;
  margin-bottom: 65px;

  &__title {
    font-weight: bold;
    font-size: 52px;
    line-height: 64px;
    letter-spacing: 0.04em;
    margin-bottom: 40px;
  }
  &__text {
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    letter-spacing: 0.04em;
    font-weight: normal;
  }
}

.news-article {
  margin-bottom: 58px;
  font-size: 16px;
  line-height: 29px;
  letter-spacing: 0.04em;
  &__item {
    margin-bottom: 20px;
  }
  &__date {
  }
}
.news-articles__pages {
  display: flex;
  justify-content: center;
}

.articles-news-images {
  display: flex;
  margin: 0 -11px;
  padding-bottom: 50px;
  &__column {
    padding: 0 11px;
    &:nth-child(1) {
      flex: 0 1 218px;
    }
    &:nth-child(2) {
      flex: 0 1 384px;
    }
    &:nth-child(3) {
      flex: 0 1 384px;
    }
  }
  &__img {
    position: relative;
    height: 100%;
    &::before {
      content: '';
      display: block;
      padding-bottom: 56%;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.articles-news__author-wrapper {
  padding: 50px 0;
  max-width: 265px;
  margin: 0 auto;
}

.articles-news-author {
  &__header {
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    &::before {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__body {
    text-align: center;
  }
  &__link {
    display: block;
    text-decoration: none;
    font-weight: bold;
    font-size: 22px;
    color: $text-color;
    margin-bottom: 10px;
  }
  &__name {
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    letter-spacing: 0.04em;
  }
}

@include media-medium {
  .article {
    padding-top: 40px;
  }
  .news-title__title {
  }
  .news-title {
    margin-bottom: 10px;
    &__title {
      font-size: 34px;
      line-height: 1.2em;
      margin-bottom: 20px;
    }
    &__text {
      font-size: 14px;
      line-height: 1.4em;
    }
  }
  .news__header {
    margin: 40px 0;
  }
}

@include media-small {
  .news {
    padding: 60px 0 20px;
  }
  .news-title {
    &__title {
      font-size: 26px;
    }
    &__text {
      display: none;
    }
  }
  .articles-news__author-wrapper {
    max-width: 160px;
    padding: 25px 0;
  }
  .articles-news-images {
    flex-wrap: wrap;
    &__column {
      padding: 10px;
      &:nth-child(1) {
        flex: 0 0 35%;
      }
      &:nth-child(2) {
        flex: 0 0 65%;
      }
      &:nth-child(3) {
        flex: 0 0 100%;
      }
    }
    &__img {
    }
  }
}
