@font-face {
  font-family: 'Century Gothic';
  src: url('../assets/fonts/CenturyGothic-regular.eot');
  src: url('../assets/fonts/CenturyGothic-regular.eot?#iefix')
      format('embedded-opentype'),
    url('../assets/fonts/CenturyGothic-regular.woff') format('woff'),
    url('../assets/fonts/CenturyGothic-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Century Gothic';
  src: url('../assets/fonts/CenturyGothic-Bold.eot');
  src: url('../assets/fonts/CenturyGothic-Bold.eot?#iefix')
      format('embedded-opentype'),
    url('../assets/fonts/CenturyGothic-Bold.woff') format('woff'),
    url('../assets/fonts/CenturyGothic-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
