.banking-details {
  max-width: 870px;
  margin: 0 auto;
  &__column {
    flex: 1 1 30%;
    margin-bottom: 20px;
    &:first-of-type {
      flex: 0 0 70%;
    }
  }
}

.banking-details-about {
  &__title {
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
  &__text {
    font-size: 17px;
    line-height: 1.2em;
    letter-spacing: 0.03em;
    color: #565656;
    margin-bottom: 10px;
  }
  &__link {
    text-decoration: none;
    font-weight: bold;
    color: #565656;

    &:hover {
      text-decoration: underline;
    }
  }
}

@include media-medium {
  .banking-details {
    &__row {
      flex-direction: column;
    }
    &__column,
    &__column:first-of-type {
      flex: 1 1 auto;
    }
  }
}
