.blog {
  padding: 64px 0 74px;
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -30px;
    margin-bottom: 46px;
  }
  &__item {
    flex: 0 0 33%;
    padding: 0 30px;
    margin-bottom: 30px;
  }
}

.blog-item {
  &__header {
    position: relative;
    &::before {
      content: '';
      display: block;
      padding-bottom: 73%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__title {
    margin: 24px 0 25px;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.04em;
  }
  &__text {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.04em;
    span {
      display: block;
    }
  }
}

.number-pages {
  display: flex;
  justify-content: center;
  &__list {
    display: flex;
    list-style: none;
    padding: 0;
  }
  &__item {
    margin-right: 40px;
  }
  &__link {
    position: relative;
    padding: 5px;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.04em;
    text-decoration: none;
    color: $text-color;
    &.active::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -6px;
      height: 1px;
      width: 100%;
      background: $text-color;
    }
  }
}

@include media-extra-large {
  .blog {
    &__item {
      flex: 0 0 50%;
    }
  }
}

@include media-medium {
  .blog {
    padding: 70px 0 30px;
    &__row {
      margin-bottom: 20px;
    }
    &__item {
      flex: 0 0 100%;
      margin: 0 auto;
      margin-bottom: 25px;
    }
  }
  .blog-item {
    &__header {
      &::before {
        padding-bottom: 56%;
      }
    }
  }
}

@include media-small {
  .blog-item {
    &__title {
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.04em;
      margin: 17px 0 11px;
    }
    &__text {
      font-size: 14px;
      line-height: 1.2em;
      letter-spacing: 0.04em;
    }
  }
}
