.customer-card {
  &__header {
    padding-bottom: 68%;
    position: relative;
  }

  &__events {
    position: absolute;
    z-index: 1;
    top: 20px;
    right: 20px;
  }
  &__body {
    padding: 24px 0 0;
  }
  &__title {
    font-weight: bold;
    font-size: 36px;
    line-height: 92.11%;
    letter-spacing: 0.04em;
    margin-bottom: 15px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 92.11%;
    letter-spacing: 0.04em;
  }
}

.customer-card-events {
  &.open {
    z-index: 5;
    .customer-card-events__list {
      visibility: visible;
      transform: scale(1) translate(-50%, 0px);
    }
    .customer-card-events__overflow {
      visibility: visible;
      opacity: 1;
    }
  }
  &__overflow {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 500vw;
    height: 500vh;
    transform: translate(-50%, -50%);
    background-color: rgba($color: #000000, $alpha: 0.2);
    z-index: 4;
    transition: $transition * 1.5;
  }
  &__icon {
    cursor: pointer;
  }
  &__list {
    visibility: hidden;
    transform: scale(0) translate(-0%, 20px);

    position: absolute;
    top: 100%;
    left: 50%;
    list-style: none;
    padding: 0;
    z-index: 999;
    transition: $transition * 0.5;
    & .customer-card-events__item {
      overflow: hidden;

      &:first-of-type {
        border-radius: 10px 10px 0 0;
      }
      &:last-of-type {
        border-radius: 0 0 10px 10px;
      }
    }
  }
  &__item {
    position: relative;
    z-index: 5;
  }
  &__event {
    display: block;
    width: 100%;
    cursor: pointer;
    padding: 10px 21px;
    border: none;
    text-decoration: none;
    background: $sub-color;
    color: $text-color;
    font-size: 15px;
    letter-spacing: 0.04em;
    transition: $transition;
    outline: none;
    text-align: left;
    &:hover {
      background: #e3e3e3;
    }
  }
}

@include media-medium {
  .customer-card-events.open {
    .customer-card-events__list {
      transform: translateY(0%);
    }
  }
  .customer-card-events__list {
    position: fixed;
    bottom: 0;
    left: 0;
    top: auto;
    visibility: visible;
    transform: translateY(100%);
    background: $sub-color;
    border-radius: 20px 20px 0 0;
    overflow: hidden;
    width: 100vw;
  }
}
