.btn {
  display: inline-block;
  border-radius: $base * 2.5;
  padding: 22px 24px;
  border: none;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  font-size: 16px;
  letter-spacing: 0.03em;
  transition: $transition;

  &--red {
    color: $sub-color;
    background: $main-color;
    font-weight: bold;
    &:hover {
      background: lighten($color: $main-color, $amount: 8%);
    }
    &:active {
      background: lighten($color: $main-color, $amount: 15%);
    }
  }
}
