.cosplayer-shape{
    &__row {
        display: flex;
    }
    &__column {
        flex: 0 0 50%;
        max-width: 50%;
        &:nth-child(2){
            padding-left: 38px;
        }
    }
    &__tabs {
        margin-bottom: 100px;
    }
    &__slider{
        height: 100%;
    }
}

.cosplayer-shape-tabs{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

.cosplayer-shape-tab {
    outline: none;
    background: none;
    border: 1px solid #4E4E4E;
    padding: 23px 46px;
    border-radius: 10px;
    cursor: pointer;
    transition: $transition;
    margin: 5px;
    color: $text-color;
    text-decoration: none;
    &:hover {
        opacity: 0.8;
        transform: scale(0.95);
    }
    &.active {
        background: #4E4E4E;
        color: #fff;
        .cosplayer-shape-tab__subtitle {
            &:nth-child(2){
                border-top: 1px solid #fff;
            }
            
        }
    }
    &__title {
        display: block;
        font-size: 18px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
    }
   
    &__subtitle {
        display: block;
        width: 80%;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.04em;
        text-align: left;
        &:nth-child(2){
            margin-top: 14px;
            padding-top: 10px;
            border-top: 1px solid #4E4E4E;
        }
        
    }
}

.cosplayer-shape-item {
    display: none;
    padding-top: 20px;
    &.open {
        display: block;
    }
}
.cosplayer-shape-slider {
    position: relative;
    width: 100%;
    height: 100%;
    .slick-list {
        overflow: hidden;
        height: 100%;
    }
    .slick-track {
        display: flex;
        height: 100%;

    }
    &__item {
        height: 100%;
        border-radius: 7px;
        overflow: hidden;
    }
    .slick-arrow{
        position: absolute;
        z-index: 5;
        top: 50%;
        transform: translateY(-50%);
        background: none;
        border: none;
        outline: none;
        transition: $transition;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
        &.slick-disabled {
            opacity: 0;
            visibility: hidden;
        }
    }
    .slick-next {
        right: 0;
    }
    .slick-prev {
        transform: translateY(-50%) rotate(180deg);
    }
}

.cosplayer-shape-about {
    background: #fff;
    padding: 54px 60px;
    border-radius: 20px;;
    &__title{
        margin-bottom: 32px;
        font-size: 30px;
        line-height: 37px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        padding-bottom: 40px;
        border-bottom: 1px solid #8D8D8D;
    }
    &__subtitle{
        font-size: 30px;
        line-height: 37px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        padding-bottom: 40px;
        border-bottom: 1px solid #8D8D8D;
    }
    &__body {
        padding-top: 53px;
    }
    &__cosplay-title{
        font-size: 24px;
        line-height: 29px;
        letter-spacing: 0.04em;
        margin-bottom: 20px;
    }
    &__text{
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.04em;
    }
}

@include media-extra-large  {
    .cosplayer-shape{
        &__tabs {
            margin-bottom: 60px;
        }
        &__row {
            display: flex;
            flex-direction: column-reverse;
        } 
        &__column {
            flex: 0 0 100%;
            max-width: 100%;
            &:nth-child(2){
                padding-left: 0px;
                margin-bottom: 26px;
            }
        }
    }
    .cosplayer-shape-tabs {
        align-items: stretch;
    }
    .cosplayer-shape-tab {
        flex: 1 1 auto;
        padding: 15px 15px;
    }
    .cosplayer-shape-slider {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
        &__wrapper {
            position: relative;
            padding-bottom: 56%;
            height: auto;
        }
        &__item {
        }
    }
  }
  


  @include media-small {
      .cosplayer-shape-about{
          padding: 27px 14px;
          font-size: 14px;;
        &__title{
            display: none;
           
        }
        &__subtitle {
            display: none;
        }
        &__cosplay-title {
            font-weight: bold;
            font-size: 20px;
            line-height: 25px;
            letter-spacing: 0.04em;
            border-bottom: none;
        }
      }
    .cosplayer-shape-tab {
        flex: 1 1 100%;
        padding: 15px 15px;
        &__subtitle {
            display: none;
        }
    }
    .cosplayer-shape-slider {
        &__wrapper {
            padding-bottom: 86%;
        }
}
  }
  