.hero-form {
  padding: 40px 0 81px;
  &__tip {
    font-size: 20px;
    line-height: 25px;
    text-align: right;
    letter-spacing: 0.01em;
    margin-bottom: 63px;
  }
  &__reset {
    display: block;
    margin: 25px 0;
    background: none;
    border: none;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    color: #808080;
    outline: none;
    cursor: pointer;
    transition: $transition;
    &:hover {
      opacity: 0.7;
    }
  }
  &__pre-footer {
    display: flex;
    justify-content: flex-end;
  }
  &__footer {
    display: flex;
    justify-content: center;
  }
}

@include media-extra-large {
}

@include media-large {
  .hero-form__tip {
    text-align: center;
    font-size: 12px;
    margin-bottom: 24px;
    margin-top: 19px;
    line-height: 12px;
  }
}

@include media-medium {
  .hero-form {
    padding: 60px 0 47px;
  }
}
