.cosplayer-about{
   
}

.cosplayer-about-item {
    &__row {
        display: flex;
    }
    &__column {
        &:first-child {
            flex: 0 0 275px;
            padding-right: 134px;
        }
    }

    &__img{ 
        padding-bottom: 100%;
        border-radius: 50%;
        overflow: hidden;
        margin-bottom: 20px;
    }
    &__city {
        margin-bottom: 43px;
    }
    &__name {
        font-size: 20px;
        letter-spacing: 0.04em;
    }
    &__person {
        font-weight: bold;
        font-size: 36px;
        line-height: 92.11%;
        letter-spacing: 0.04em;
    }
    &__about {
        padding: 54px 94px;
        background: #fff;
        border-radius: 20px;
    }
    &__title {
        font-weight: bold;
        font-size: 36px;
        line-height: 44px;
        letter-spacing: 0.04em;
        margin-bottom: 27px;
    }
    &__text {
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.04em;
        p  + p{
            margin-top: 30px;
        }
    }
}

.cosplayer-city {
    background: #B4B4B4;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    padding: 7px 14px;
    border-radius: 5px;
    text-align: center;
} 


@include media-extra-large {
    .cosplayer-about-item {
        &__row {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__column {
            &:first-child {
                flex: auto;
                max-width: 250px;
                padding-right: 0;
                margin-bottom: 60px;
            }
        }
        &__name {
            text-align: center;
        }
        &__person {
            text-align: center;
        }
        &__about {
            padding: 24px 34px;
        }
    }
  }

  @include media-small {
    .cosplayer-about-item {
        &__name {
            font-size: 20px;
        }
        &__person {
            font-size: 30px;;
        }
        &__about {
            display: none;
        }
        &__column {
            &:first-child {
                max-width: 213px;
                margin-bottom: 0px;
            }
        }
    }
   
  }