.pro-cosplay-card {
  background: $sub-color;
  &__header {
    position: relative;
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &::before {
      content: '';
      display: block;
      padding-bottom: 122%;
    }
  }
  &__body {
    padding: 38px 36px 50px 36px;
  }

  &__social {
    outline: none;
    letter-spacing: 0.01em;
    font-weight: lighter;
    color: $text-color;
    text-decoration: none;
    transition: $transition;
    &:hover {
      opacity: 0.7;
    }
  }
  &__title {
    font-size: 20px;
    letter-spacing: 0.01em;
    margin: 15px 0 7px;
    padding: 0;
  }
  &__text {
    max-width: 290px;

    font-size: 20px;
    letter-spacing: 0.01em;
    margin: 0;
    margin-bottom: 27px;
  }
}

@include media-medium {
  .pro-cosplay-card {
    &__header {
      &::before {
        padding-bottom: 107%;
      }
    }
    &__body {
      padding: 20px 23px;
    }
    &__social {
      font-size: 16px;
    }
    &__text {
      font-size: 14px;
      line-height: 1.2em;
    }
    &__link {
      padding: 10px 14px;
      font-size: 14px;
    }
  }
}
