.title {
  margin: 0;
  font-weight: normal;
  margin-bottom: 81px;
  &__icon {
    width: 133px;
    height: 78px;
    margin-bottom: 57px;
    object-fit: contain;
  }
  &__text {
    margin: 0 auto;
    font-size: 52px;
    line-height: 64px;
    text-align: center;
    letter-spacing: 0.04em;
    max-width: 1158px;
    width: 100%;
    text-transform: uppercase;
  }
  &__subtext {
    max-width: 993px;
    margin: 63px auto 0 auto;
    text-align: center;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    letter-spacing: 0.04em;
  }
  &--center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@include media-extra-large {
  .title {
    &__text {
      font-size: 36px;
      line-height: 1.3em;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__icon {
      width: 73px;
      height: 48px;
      margin-bottom: 37px;
    }
  }
}

@include media-medium {
  .title {
    margin-bottom: 40px;
  }
  .title__text {
    font-size: 26px;
  }
  .title__subtext {
    margin-top: 40px;
    font-size: 16px;
    line-height: 1.2em;
  }
}

@include media-small {
  .title {
    margin-bottom: 20px;
  }
  .title__icon {
    margin-bottom: 18px;
  }
  .title__text {
    font-size: 26px;
  }
  .title__subtext {
    margin-top: 25px;

    font-size: 10px;
  }
}
