.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.content {
  padding-top: 50px;
  flex: 1 1 auto;
}

$container-padding: 80px;

.container {
  max-width: 1337px + $container-padding * 2;
  padding: 0 $container-padding;
  margin: 0 auto;
}

.section {
  padding: 80px 0;
}

.row {
  display: flex;
  &.wrap {
    flex-wrap: wrap;
  }
  &.justify-between {
    justify-content: space-between;
  }
  &.center {
    justify-content: center;
  }
}
@include media-max-extra-large {
  .container {
    padding: 0 $container-padding;
    max-width: 1200px + $container-padding * 2;
  }
}
@include media-extra-large {
  .container {
    max-width: 1020px + $container-padding * 2;
  }
}

$container-mg-padding: 10px;

@include media-large {
  .container {
    max-width: 760px + $container-mg-padding * 2;
    padding: 0 $container-mg-padding;
  }
  .section {
    padding: 60px 0;
  }
}

@include media-medium {
  .section {
    padding: 40px 0;
  }
  .content {
    padding-top: 60px;
  }
}

@include media-small {
  .container {
    max-width: 480px + $container-mg-padding * 2;
  }
  .section {
    padding: 25px 0;
  }
}
