* {
  box-sizing: border-box;
}

body {
  font-family: $main-font;
  background: $bg-color;
  min-width: 320px;
  &.locked {
    overflow: hidden;
  }
}

.slick-list {
  overflow: hidden;
}

.slick-slide {
  outline: none;
}

.slick-track {
  display: flex;
}

.ibg {
  position: relative;
  & > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.row {
  display: flex;
  &.align-center {
    align-items: center;
  }
  &.justify-between {
    justify-content: space-between;
  }

  &.wrap {
    flex-wrap: wrap;
  }
}
