.input-field--block {
  background: $sub-color;
  padding: 37px 47px;
  border-radius: $base * 6;

  .input-field {
    &__title {
      font-size: 18px;
      line-height: 22px;
      letter-spacing: 0.03em;
      padding-bottom: 17px;
      margin-bottom: 16px;
      border-bottom: 1px solid #999999;
    }

    &__input-wrapper {
      position: relative;

      &.focus {
        .input-field__hints {
          opacity: 1;
          visibility: visible;
        }
      }

      &::before {
        content: "";
        position: absolute;
        z-index: 1;
        top: 50%;
        transform: translateY(-25%);
        right: 20px;
        border: 8px solid transparent;
        border-top: 10px solid #c4c4c4;
        pointer-events: none;
      }
    }

    &__select,
    &__input {
      position: relative;
      border-radius: $base * 6;
      background: $gray-color;
      outline: none;
      border: none;
      width: 100%;
      padding: 10px 20px;
    }

    &__hints {
      max-height: 200px;
      overflow-y: auto;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 100%;
      transform: translateY(-20px);
      z-index: 100;
      width: 100%;
      background: $sub-color;
      list-style: none;
      background: #ffffff;
      padding: 0;
      box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
      transition: $transition;
    }

    &__hint {
      position: relative;
      padding: 20px 17px;

      transition: $transition * 0.1;
      cursor: pointer;

      &:last-child {
        &::before {
          display: none;
        }
      }

      &::before {
        content: "";
        position: absolute;
        width: 82%;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0.7;
        border-bottom: 1px solid #999999;
      }

      &:hover {
        background: $bg-color;
      }
    }
  }
}

.input-field {
  &__header {
    position: relative;
    display: flex;
    align-items: center;
    padding-bottom: 14px;
  }
  &__title {
    font-size: 24px;
    letter-spacing: 0.03em;
    padding-right: 10px;
  }

  &__input,
  &__textarea,
  &__select {
    width: 100%;
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 6px;
    padding: 15px;
    outline: none;
    font-size: 16px;
    background: none;

    &:focus {
      border-color: $main-color;
    }
  }

  &__textarea {
    resize: none;
  }
}

.c-checkbox {
  &__input {
    display: none;

    &:checked {
      & + .c-checkbox__body {
        .c-checkbox__checkbox {
          &::after {
            content: "\2714";
          }
        }
      }
    }
  }

  &__body {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__checkbox {
    display: block;
    border: 1px solid #000000;
    flex: 0 0 21px;
    height: 21px;
    margin-right: 21px;
    text-align: center;
    line-height: 21px;
    transform: translateY(3px);
  }

  &__title {
    font-size: 24px;
    line-height: 1em;
    letter-spacing: 0.03em;
  }
}

.tip {
  display: flex;
  position: relative;
  cursor: pointer;
  &::before {
    content: attr(data-text);
    width: 200px;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    background: $sub-color;
    padding: 15px 10px;
    border-radius: 20px;
    transition: $transition;
    opacity: 0;
    visibility: hidden;
  }
  &:hover,
  &:focus {
    &::before {
      bottom: 130%;
      opacity: 1;
      visibility: visible;
    }
  }
}

.social-input {
  display: flex;
  align-items: center;

  &__icon {
    margin-right: 10px;
    padding: 10px;
    border-radius: 50%;
    background-color: $sub-color;
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }
  }
  &__wrapper {
    flex: 1 1 auto;
    position: relative;
  }
  &__text {
    display: inline-block;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    letter-spacing: 0.03em;
    pointer-events: none;
    &::before {
      content: "";
      position: absolute;
      bottom: 0px;
      width: 100%;
      height: 1px;
      background: #000;
    }
  }
  &__input {
    border: none;
    background: none;
    outline: none;
    width: 100%;
    padding: 10px 15px;
    border-radius: 10px;
    cursor: pointer;
    transition: $transition;
    &::placeholder {
      font-size: 0;
    }
    &:hover {
      background: rgba($color: $sub-color, $alpha: 0.5);
    }
    &:focus,
    &:not(:placeholder-shown) {
      & + .social-input__text {
        display: none;
      }
    }
    &:focus {
      background: rgba($color: $sub-color, $alpha: 0.8);
      box-shadow: 0 0 5px rgba($color: $text-color, $alpha: 0.3);

      &::placeholder {
        font-size: 0;
      }
    }
  }
}

@include media-extra-large {
  .input-field--block {
    padding: 17px;
  }
}

@include media-large {
  .input-field--block {
    padding: 13px 15px;
  }
}

@include media-medium {
  .input-field {
    .tip {
      &__icon {
        width: 16px;
        height: 16px;
      }
    }
    &__header {
      padding-bottom: 5px;
    }
    &__title {
      font-size: 16px;
    }
  }
  .c-checkbox {
    &__title {
      font-size: 18px;
    }
  }
}
