.header {
  position: fixed;
  z-index: 800;
  top: 0;
  left: 0;
  transition: $transition;
  .header-header__text,
  .header-menu__text {
    max-width: 0;
    transition: $transition * 1.5;
    opacity: 0;
    white-space: nowrap;
  }
  &__container {
    position: absolute;
    z-index: 1;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    height: 100vh;
    background: $sub-color;
    transition: $transition * 1.5;
  }
  &__overlay {
    visibility: hidden;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    transition: $transition * 1.5;
  }
}

.header-header {
  background: $bg-color;
  padding: 21px 11px;
  img {
    width: 43px;
    height: 26px;
    transition: $transition * 1.5;
  }
  &__link {
    color: $text-color;
    text-decoration: none;
  }
  a {
    display: flex;
    text-align: center;
    align-items: center;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.01em;
    text-decoration: none;
    color: $text-color;
  }
}

.header-menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  &__item {
    &--close {
      display: none;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    width: 100%;
    text-align: center;
    padding: 24px 15px 24px 23px;
    font-weight: bold;
    font-size: 16px;
    line-height: 0.5em;
    letter-spacing: 0.01em;
    color: #a7a7a7;
    text-decoration: none;
    cursor: pointer;
    transition: $transition * 1.5;
    img {
      width: 18.3px;
      height: 18.2px;
      opacity: 0.7;
      transition: $transition;
    }
    &:hover {
      background: $bg-color;
      color: $text-color;
    }
  }
}

// HOVER & OPEN

.header:hover {
  .header-header {
    img {
      margin-right: 14px;
    }
  }
  .header-header__text,
  .header-menu__text {
    max-width: 300px;
    opacity: 1;
  }
  .header-menu {
    &__link {
      img {
        margin-right: 23.7px;
      }
    }
  }
}

.header-menu-mobile {
  display: none;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 0;
  background: $sub-color;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  &__btn {
    background: none;
    border: 0;
    cursor: pointer;
    outline: none;
    transition: $transition;
    &:hover {
      opacity: 0.6;
    }
    span {
      display: block;
      width: 25px;
      height: 2px;
      margin-bottom: 5px;
      background: $text-color;
    }
  }
}

@include media-large {
  .header-menu-mobile {
    display: block;
  }
  .header.open {
    .header__container {
      position: absolute;
      transform: translateX(0%);
    }
    .header__overlay {
      visibility: visible;
    }
  }
  .header {
    &__container {
      transform: translateX(-105%);
    }
    .header-header {
      img {
        margin-right: 14px;
      }
    }
    .header-header__text,
    .header-menu__text {
      max-width: 300px;
      opacity: 1;
    }
    .header-menu {
      &__item--close {
        display: block;
      }
      &__link {
        padding: 20px 20px;

        img {
          margin-right: 23.7px;
        }
      }
    }
  }
}
