.how-work {
  padding: 80px 0;
  &__row {
    display: flex;
    margin: 0 -24.5px;
  }
  &__item {
    flex: 0 0 33%;
    padding: 0 24.5px;
    .how-item {
      height: 100%;
    }
  }
}

.how-item {
  background: $sub-color;
  padding: 58px 54px;
  border-radius: 10px;
  &__title {
    font-weight: 700;
    font-size: 28px;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 18px;
    line-height: 29px;
    // letter-spacing: 1%;
    max-width: 240px;
    margin-bottom: 28px;
  }
  &__link {
    font-size: 18px;
    color: $main-color;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}
@include media-max-extra-large {
  .how-item {
    &__title {
      font-size: 24px;
    }
    &__text {
      font-size: 16px;
      line-height: 1.4em;
    }
  }
}
@include media-extra-large {
  .how-work {
    &__row {
      flex-wrap: wrap;
    }
    &__item {
      flex: 0 0 100%;
      margin-bottom: 16px;
    }
  }
  .how-item {
    &__title {
      font-size: 24px;
    }
    &__text {
      max-width: 400px;
    }
  }
}

@include media-small {
  .how-work {
    padding: 47px 0 48px;
    &__title {
      margin-bottom: 30px;
    }
  }
  .how-item {
    padding: 24px 30px;
    &__title {
      font-weight: 600;
      font-size: 16px;
      margin-bottom: 14px;
    }
    &__text {
      font-size: 12px;
      line-height: 1.2em;
      margin-bottom: 12px;
    }
    &__link {
      font-size: 12px;
    }
  }
}
