.setting-form {
  &__row {
    justify-content: space-between;
    margin: 0 -10px;
  }

  &__column {
    flex: 0 0 50%;
    max-width: 561px;
    margin-bottom: 36px;
    padding: 0 10px;
  }
  &__img-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 70px;
  }
  &__footer {
    display: flex;
    justify-content: center;
    padding-bottom: 74px;
  }
  &__submit {
    padding: 20px 40px;
  }
}

.setting-form-img {
  &__input {
    display: none;
  }

  &__body {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__img-wrapper {
    width: 168px;
    height: 168px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 24px;
  }

  &__title {
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.04em;

    img {
      margin-right: 10px;
    }
  }
}

.setting-form-variables {
  &__title {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.03em;
    margin-bottom: 19px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
  }

  &__column {
    flex: 0 0 50%;
    margin-bottom: 25px;
  }
}

.setting-form-social {
  display: flex;
  flex-wrap: wrap;
  &__input {
    flex: 0 0 50%;
    margin-bottom: 20px;
  }
}

.setting-form__about-textarea {
  height: 261px;
}

.setting-form__input-field--double {
  margin-bottom: 40px;
}

@include media-medium {
  .setting-form {
    &__column {
      flex: 0 0 100%;
      max-width: none;
    }
  }
  .setting-form__about-textarea {
    height: 161px;
  }
}

@include media-small {
  .setting-form-social {
    &__input {
      flex: 1 1 auto;
    }
  }
  .setting-form {
    &__column {
      margin-bottom: 21px;
    }
  }
  .setting-form__img-wrapper {
    margin: 60px 0 30px;
  }

  .setting-form-img {
    &__body {
      flex-direction: column;
    }
    &__img-wrapper {
      margin: 0;
      margin-bottom: 15px;
      width: 124px;
      height: 124px;
    }
    &__title {
      font-size: 18px;
    }
  }
  .setting-form-variables__column {
    flex: 0 0 100%;
  }
}
