.cosplay-card-search {
  &__header {
    position: relative;
    border-radius: 13px;
    overflow: hidden;
    &::before {
      content: '';
      display: block;
      padding-bottom: 96%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__status {
    position: absolute;
    z-index: 1;
    top: 15px;
    left: 15px;
    background: gray;
    border-radius: 5px;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.03em;
    padding: 5px 14px;
    &--pro {
      background: #ff9900;
    }
  }
  &__body {
    padding-top: 24px;
  }
  &__name {
    display: block;
    font-size: 18px;
    line-height: 17px;
    letter-spacing: 0.04em;
    margin-bottom: 10px;
    text-decoration: none;
    color: $text-color;
  }
  &__person {
    font-weight: bold;
    font-size: 30px;
    line-height: 92.11%;
    letter-spacing: 0.04em;
  }
}

@include media-medium {
  .cosplay-card-search {
    &__body {
      padding-top: 15px;
    }
    &__name {
      font-size: 14px;
      margin-bottom: 5px;
    }
    &__person {
      font-size: 16px;
    }
  }
}
