.cosplay-block-form {
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -$base * 7.5;
  }
  &__item {
    flex: 0 0 25%;
    padding: $base * 7.5;
  }
  &__submit {
    padding: 22px 93px;
  }
}

@include media-max-extra-large {
  .cosplay-block-form {
    &__row {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -$base * 3;
    }
    &__item {
      flex: 0 0 33%;
      padding: $base * 3;
    }
  }
}

@include media-large {
  .cosplay-block-form {
    &__item {
      flex: 0 0 50%;
    }
  }
}

@include media-small {
  .cosplay-block-form {
    &__item {
      flex: 0 0 80%;
      margin: 0 auto;
    }
  }
}
