// COLORS
$main-color: #cd0707;
$sub-color: #fff;
$gray-color: #f2f2f2;
$text-color: #000000;
$bg-color: $gray-color;

// FONTS
$main-font: 'Century Gothic', sans-serif;

// MAIN
$base: 4px;
$transition: 0.2s;

// MEDIA
$max-extra-large: 1460px;
$extra-large: 1140px;
$large: 960px;
$medium: 720px;
$small: 560px;

@mixin media-max-extra-large {
  @media (max-width: $max-extra-large) {
    @content;
  }
}

@mixin media-extra-large {
  @media (max-width: $extra-large) {
    @content;
  }
}

@mixin media-large {
  @media (max-width: $large) {
    @content;
  }
}

@mixin media-medium {
  @media (max-width: $medium) {
    @content;
  }
}

@mixin media-small {
  @media (max-width: $small) {
    @content;
  }
}
