.feedback-form {
  max-width: 870px;
  margin: 0 auto;
  margin-bottom: 100px;
  &__row {
    margin-bottom: 76px;
  }
  &__column {
    flex: 1 1 auto;
    &:first-of-type {
      padding-right: 49px;
      .input-field {
        margin-bottom: 29px;
      }
    }
    textarea {
      height: 294px;
    }
  }
  &__submit {
    padding: 15px 45px;
  }
}

@include media-medium {
  .feedback-form {
    max-width: none;
    margin-bottom: 30px;
    &__row {
      margin-bottom: 46px;
      flex-direction: column;
    }
    &__column:first-of-type {
      padding: 0;
    }
    textarea {
      height: 150px;
    }
  }
}
