.timeline {
  padding: 80px 0 90px;
  overflow: hidden;
  &__row {
    position: relative;
    display: flex;
    max-width: 1300px;
    margin: 0 auto;
    &-wrapper {
      padding-bottom: 327px;
    }
    &::before {
      content: '';
      position: absolute;
      top: 61%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      border-bottom: 3px solid #c4c4c4;
    }
  }
  &__item {
    flex: 0 0 25%;
    padding: 0 76px;
  }
  &__footer {
    display: flex;
    justify-content: center;
  }
}

.timeline-item {
  position: relative;
  cursor: pointer;
  &__date {
    text-align: center;
    margin-bottom: 15px;
    font-size: 24px;
    letter-spacing: 0.01em;
  }
  &__header {
    width: 170px;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
    &::before {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__body {
    display: none;
    position: absolute;
    top: 240px;
    left: 40px;

    width: 700px;
    z-index: -1;
    background: $sub-color;
    padding: 46px 12px 39px 58px;
    border-radius: 10px;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 20px;
      transform: translateY(-90%);
      border: 20px solid transparent;
      border-bottom: 20px solid $sub-color;
    }
    &.active {
      display: block;
    }
  }
  &__title {
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 24px;
    line-height: 29px;
    max-width: 560px;
  }
}
@media (min-width: $max-extra-large) {
  .timeline-item {
    &--right {
      .timeline-item__body {
        left: auto;
        right: 40px;
        &::before {
          left: auto;
          right: 20px;
        }
      }
    }
  }
}

@include media-max-extra-large {
  .timeline__row {
    overflow: hidden;
  }
  .timeline__row-wrapper {
    padding: 0;
  }
  .timeline {
    &__row {
      padding: 15px 0 0 0;
      flex-direction: column;
      align-items: flex-start;
      &::before {
        display: none;
      }
    }
    &__item {
      margin-bottom: 25px;
    }
  }
  .timeline-item {
    position: relative;
    margin-bottom: 25px;
    padding: 5px 0;
    background: $bg-color;
    &::after {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 0%;
      height: 200%;
      border-left: 1px solid #c4c4c4;
    }
    &__date {
      font-size: 16px;
      line-height: 12px;
      text-align: center;
      letter-spacing: 0.01em;
    }
    &__header {
      width: 130px;
    }
    &__body {
      z-index: 1;
      top: 50%;
      right: auto;
      left: 100%;
      width: 500px;
      transform: translateX(10px) translateY(-50%);
      &::before {
        border: none;
        top: 50%;
        left: 0;
        border: 20px solid transparent;
        border-right: 30px solid $sub-color;
        transform: translateX(-100%);
      }
    }
  }
}

@include media-extra-large {
  .timeline__item {
    padding: 0;
  }
}

@include media-medium {
  .timeline {
    padding: 46px 0 50px;
  }
  .timeline__row {
    padding-top: 60px;
    margin-bottom: 10px;
  }
  .timeline-item__header {
    width: 50px;
  }
  .timeline-item__body {
    width: 201px;
    padding: 23px 19px;
    &::before {
      border: 10px solid transparent;
      border-right: 20px solid $sub-color;
    }
  }
  .timeline-item__title {
    font-size: 16px;
    line-height: 19px;
  }
  .timeline-item__text {
    font-size: 12px;
    line-height: 12px;
  }
}

@include media-small {
  .timeline__btn {
    max-width: 262px;
    width: 100%;
    padding: 12px 18px;
    text-align: center;
    font-size: 14px;
  }
}
