.pro-account {
  padding: 83px 0 89px;
  &__title {
    margin-bottom: 63px;
  }
  &__item {
    padding: 0 $base * 10;
  }
  .slick-slider {
    position: relative;
    max-width: 1300px;
    margin: 0 auto;
  }
  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    outline: none;
    cursor: pointer;
    border: none;
    background: none;
    transition: $transition;
    &:hover {
      opacity: 0.6;
    }
    img {
      width: 57px;
      height: 57px;
    }
  }
  .slick-next {
    right: -47px;
  }
  .slick-prev {
    left: -47px;
  }
}
@include media-max-extra-large {
  .pro-account {
    &__item {
      padding: 0 $base * 4;
    }
    .slick-slider {
      position: relative;
      max-width: 1200px;
      margin: 0 auto;
    }
    .slick-next {
      right: -0px;
    }
    .slick-prev {
      left: -0px;
    }
  }
}

@include media-medium {
  .pro-account {
    padding: 49px 0 48px;
    &__title {
      margin-bottom: 30px;
    }
    .slick-arrow img {
      width: 30px;
      height: 30px;
    }
  }
}

@include media-small {
  .pro-account {
    &__item {
      padding: 0 $base * 10;
    }
  }
}
