.cosplayer-feedback {

}

.cosplayer-feedback-form {
   
    &__header{
        display: flex;
        flex-direction: column;
        margin-bottom: 61px;
    }
    &__input {
        display: block;
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.04em;
        color: #B7B7B7;
        background: #fff;
        border-radius: 10px;
        padding: 24px 20px;
        outline: none;
        border: none;
        &:focus::placeholder {
            font-size: 0;
        }
    }
    &__hint {
            font-size: 18px;
        line-height: 22px;
            letter-spacing: 0.04em;
        color: #969696;
    }
    &__footer{
             display: flex;
        justify-content: center;
    }
    &__submit{
        padding: 22px 60px;
    }
}

@include media-small {
    .cosplayer-feedback-form {
        &__header {
            margin-bottom: 28px;
        }
        &__input {
            margin-bottom: 5px;
        }
        &__hint {
            font-size: 12px;
        }
    }   
   
}